.contactCard {
  margin: 60px;
  width: 80%;
  margin-bottom: 30px;
  border-radius: 6px;
  color: rgba(0, 0, 0, 0.87);
  background: #fff;
  text-align: center;
  margin-top: 8vw !important;
}

.contactHeader {
  margin: -55px 10px;
  padding: 25px 10px;
  background: #ff8b9e;
  text-align: center;
  border-radius: 3px;
  color: white;
  font-size: 22px;
  font-family: "Roboto", sans-serif;
  text-transform: uppercase;
}

.email {
  margin-top: 70px;
  margin-left: 8vw;
}

.name,
.message {
  margin-left: 8vw;
}

.map {
  width: 500px;
  height: 477px;
  margin-top: 6rem;
  border-color: transparent;
  margin-bottom: 4rem;
}

button {
  height: unset;
}
.btnContact {
  background-color: #158994;
  margin-bottom: 2vw;
  margin-top: 1vw;
  width: auto;
}

